body {
  margin: 0;
}

@keyframes rotate {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.stopwatch {
  box-sizing: border-box;
  justify-content: center;
  align-items: center;
  height: 100vmin;
  display: flex;
  position: relative;
}

.stopwatch__table {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
}

.stopwatch__center {
  z-index: 1;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}

.stopwatch__minutes {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate;
  position: absolute;
  bottom: 50%;
}

.stopwatch__seconds {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate;
  position: absolute;
  bottom: 50%;
}

.stopwatch--speed-up .stopwatch__seconds {
  animation: 10s linear infinite rotate;
}

.stopwatch--speed-up .stopwatch__minutes {
  animation: 600s steps(60, end) infinite rotate;
}

